body{
    font-family:Arial, Helvetica, sans-serif;
}
h3, h4, p{
    margin:0;
}
h1{
    font-weight:lighter;
    font-size:25px;
    margin:0 0 10px 0;
}
.App{
    display:flex;
    flex-direction: column;
    align-items: center;
}
.header{
    margin:0 0 20px 0;
    text-align:center;
}
.header input{
    width:600px;
    height:25px;
}
.mainbody{
    display:flex;
}
.VideoDescription{
    margin:0 20px 20px 0;
}
.the_video_iframe{
    width:1280px;
    height:720px;
    margin:0 0 15px 0;
}
.Video{
    display:flex;
    margin:0 0 7px 0;
    width:500px;
}
.Video:hover{
    cursor: pointer;
}
.titlechannel{
    display:flex;
    flex-direction: column;
}
.Video img{
    height:150px;
    margin:0 10px 0 0;
}
.Video p{
    font-size:14px;
    margin:0 0 5px 0;
}
@media only screen and (max-width: 1600px) {
    .the_video_iframe{
        width:854px;
        height:480px;
    }
}
@media only screen and (max-width: 1420px) {
    .mainbody{
        display:flex;
        flex-direction: column;
    }
    .the_video_iframe{
        width:854px;
        height:480px;
    }
    .Video{
        width:100%;
    }
}
@media only screen and (max-width: 800px) {
    .the_video_iframe{
        width:100%;
        height:360px;
    }
    .VideoList{
        display:flex;
        flex-direction: column;
        align-items: center;
    }
    .Video{
        display:flex;
        margin:0 0 7px 0;
        width:100%;
    }
    .header input{
        width:300px;
        height:25px;
    }
}